.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 15%;
  background-color: #40513b;
  height: 100vh;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.icon img {
  width: 4vh;
  height: 4vh;
  position: relative;
  left: 1rem;
  top: 0.75rem;
  cursor: pointer;
}

.navbarLinks {
  margin-top: 25vh;
  width: 100%;
}

.itemWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  color: #edf1d6;
  width: 100%;
  cursor: pointer;
  padding: 0.75rem 0;
  margin-bottom: 0.5rem;
}

.itemWrapper:hover {
  background-color: #609966;
  border-radius: 10px;
}

.activeItem {
  background-color: #609966;
  border-radius: 10px;
}
